___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-color: rgb(3, 51, 112);
}

header {
    text-align: center;
    color: white;
}

h2{
    text-align: center;
    color: white;
}

main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

button {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    width: 150px;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 12px;
}

.button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.board-container {
    display: flex;            
    gap: 200px; 
    justify-content: center;           
}

.board {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    gap: 2px;
    width: 300px;
    height: 300px;
}

.board.disabled {
    pointer-events: none;
}

.cell {
    width: 30px;
    height: 30px;
    border: 1px solid #000000;
    display: inline-block;
}

.cell.empty {
    background-color: rgb(99, 205, 231);
}

.cell.ship {
    background-color: rgb(87, 85, 85);
}

.cell.hit {
    background-color: red;
}

.cell.miss {
    background-color: rgb(255, 255, 255);
}

.cell.sunk {
    background-color: black;
}

#game-status {
    text-align: center;
    color: rgb(25, 228, 86);
    background-color: #000000;
    border: 2px solid white;
    border-radius: 12px;
    width: 300px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,iCAAiC;AACrC;;AAEA;IACI,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,oCAAoC;IACpC,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,qBAAqB;IACrB,qBAAqB;IACrB,eAAe;IACf,eAAe;IACf,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,UAAU;IACV,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sCAAsC;IACtC,QAAQ;IACR,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,uBAAuB;IACvB,yBAAyB;IACzB,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;AACpB","sourcesContent":["body {\n    background-color: rgb(3, 51, 112);\n}\n\nheader {\n    text-align: center;\n    color: white;\n}\n\nh2{\n    text-align: center;\n    color: white;\n}\n\nmain {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\nbutton {\n    background-color: rgb(255, 255, 255);\n    color: rgb(0, 0, 0);\n    width: 150px;\n    padding: 10px 20px;\n    text-align: center;\n    text-decoration: none;\n    display: inline-block;\n    font-size: 16px;\n    margin: 4px 2px;\n    cursor: pointer;\n    border-radius: 12px;\n}\n\n.button-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.board-container {\n    display: flex;            \n    gap: 200px; \n    justify-content: center;           \n}\n\n.board {\n    display: grid;\n    grid-template-columns: repeat(10, 1fr);\n    gap: 2px;\n    width: 300px;\n    height: 300px;\n}\n\n.board.disabled {\n    pointer-events: none;\n}\n\n.cell {\n    width: 30px;\n    height: 30px;\n    border: 1px solid #000000;\n    display: inline-block;\n}\n\n.cell.empty {\n    background-color: rgb(99,